import React, { FC } from "react";
import { ButtonProps, ButtonWrapProps, StyledButtonOwnProps } from "./button.types";
import {styled} from '@stitches/react';
import type * as Stitches from '@stitches/react';

 const ButtonWrap = styled('button', {
    paddingLeft: 'var(--padding)',
    variants:{
        color: {
            red: {
                backgroundColor: "red"
            },
            green: {
                backgroundColor: "LightGreen"
            },
        type: {
            primary: {

            },
            secondary: {

            }
        }
            
        }
    }
 });

export type ButtonWrapVariants = Stitches.VariantProps<typeof ButtonWrap>

type StyledButtonProps = StyledButtonOwnProps & ButtonWrapVariants;

const Button:FC<StyledButtonOwnProps & ButtonWrapVariants> = (props: StyledButtonOwnProps & ButtonWrapVariants) => {
    const { label, color, type} = props;
    return <ButtonWrap type={type}  color={color}> { label } </ButtonWrap>;
}

export default Button;